.photo-upload {
  text-align: center;
}

#photoupload {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-align: center;
  -webkit-box-pack: center;
  z-index: 10000;
}

#welcome{
  font-size: -webkit-xxx-large;
  color: #0fbfd6;
  font-weight: 200;
}

#photoupload .fileBrowser {
  font-size: 1em;
  padding: 1em;
  border-radius: 10px;
  background: #00d8ff;
  opacity: 1;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  -webkit-box-shadow: 0 1px 8px 1px #bbb;
          box-shadow: 0 1px 8px 1px #bbb;
  -webkit-transition: opacity 0.2s ease-in-out;
  -o-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
  margin-left: 10px;
}

#photoupload .fileBrowser:hover {
  opacity: 1;
}

#photoupload .input {
  position: relative;
  display: block;
  font-size: 3em;
}



